body.isScroll {
    overflow: hidden;
}

header {
    position: relative;
    width: 100%;
    background: #f6f8f6;
    z-index: 1;
    &::before {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        left: -100%;
        width: 100%;
        height: 0;
        background: linear-gradient(270deg, transparent, #cddc39, transparent);
    }
    &::after {
        content: "";
        display: block;
        position: absolute;
        top: 0;
        right: 0%;
        width: 100%;
        height: 0;
        background: linear-gradient(270deg, transparent, #4caf50, transparent);
    }
    &.sub {
        .inner {
            padding: 20px;
        }
    }
    .inner {
        display: flex;
        width: 1328px;
        margin: 0 auto;
        padding: 40px 0;
        align-items: center;
    }

    .logo {
        width: 31%;
        font-size: 37px;
        span {
            color: #4a6869;
        }
    }
    .gnb {
        position: relative;
        width: 46%;
        display: flex;
        justify-content: space-between;
        .line {
            position: absolute;
            left: 0;
            bottom: -5px;
            width: 100px;
            height: 0px;
            background: #4a6869;
            transition: all 0.5s;
            transition-timing-function: cubic-bezier(ease-in-out);
            &.on {
                height: 5px;
            }
        }
        li {
            position: relative;
            font-size: 19px;
            color: #4a6869;
            font-weight: 500;
            overflow: hidden;
            transition: all 0.5s;
            a {
                transition: all 0.5s;
                &:hover {
                    color: rgb(43, 174, 41);
                }
            }
        }
    }

    .totalMenuBtn {
        display: none;
        border: none;
        position: relative;
        width: 35px;
        height: 25px;
        border-bottom: solid 2px #000;
        background-color: transparent;
        cursor: pointer;
        transition: all 0.5s;
        &::before {
            content: "";
            display: block;
            position: absolute;
            top: 0;
            width: 100%;
            height: 2px;
            background: #000;
            transform-origin: left;
            transition: all 0.5s;
        }
        &::after {
            content: "";
            display: block;
            position: absolute;
            top: 50%;
            width: 100%;
            height: 2px;
            background: #000;
            transform-origin: left;
            transition: all 0.5s;
        }

        &.on {
            width: 30px;
            height: 30px;
            border: none;
            &::before {
                width: 140%;
                transform: rotateZ(45deg);
                background: #fff;
            }
            &::after {
                width: 140%;
                top: calc(100% - 1px);
                transform: rotateZ(-45deg);
                background: #fff;
            }
        }
    }
}

@keyframes mobileLeftBg {
    0% {
        height: 100vh;
        left: -100%;
    }
    100% {
        height: 100vh;
        left: 100%;
    }
}

@keyframes mobileRightBg {
    0% {
        height: 100vh;
        right: -100%;
    }
    100% {
        height: 100vh;
        right: 200%;
    }
}

@keyframes mobileMainBg {
    0% {
        opacity: 0;
        left: 50%;
        width: 0;
        filter: blur(28px);
        transform: translateX(-50%) scale(1.7);
    }
    50% {
        filter: blur(28px);
        transform: translateX(-50%) scale(1.7);
    }
    99% {
        filter: blur(0px);
        transform: translateX(-50%) scale(1.7);
    }
    100% {
        opacity: 1;
        left: 50%;
        filter: blur(0px);
        transform: translateX(-50%) scale(1);
        width: 100%;
    }
}

@keyframes menuFadeIn {
    0% {
        opacity: 0;
        transform: translateX(120px);
    }
    100% {
        transform: translateX(0px);
        opacity: 1;
    }
}

@media screen and (max-width: $xl) {
    header {
        .inner {
            width: 100%;
            padding: 40px 20px;
        }

        .logo {
            width: 30%;
            margin-left: 40px;
            font-size: 28px;
        }
        .gnb {
            width: 55%;
            li {
            }
        }
    }
}

@media screen and (max-width: $md) {
    header {
        .inner {
            justify-content: space-between;
            padding: 20px;
        }

        .logo {
            width: initial;
            margin-left: 0;
        }
        .gnb {
            display: none;
        }

        .totalMenuBtn {
            margin-right: 0px;
            display: block;
        }
    }

    header.mobile {
        display: flex;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 34;
        align-items: center;
        justify-content: center;
        background-color: transparent;

        &::before {
            animation: 1.5312s cubic-bezier(0.33, 0, 0.51, 1) 0s 1 normal
                forwards mobileLeftBg;
        }
        &::after {
            animation: 2.32s cubic-bezier(0.33, 0, 0.51, 1) 0s 1 normal forwards
                mobileRightBg;
        }
        .totalMenuBtn {
            position: absolute;
            right: 23px;
            top: 30px;
            &::before {
                color: #fff;
            }
            &::after {
                color: #fff;
            }
        }

        .inner {
            position: absolute;
            display: flex;
            width: 0;
            height: 100%;
            left: 0;
            top: 0;
            padding: 0;
            background: #000000b0;
            -webkit-backdrop-filter: blur(3px);
            backdrop-filter: blur(3px);
            justify-content: center;
            overflow: hidden;
            animation: 1s cubic-bezier(0.33, 0, 0.51, 1) 0.5s 1 normal both
                mobileMainBg;
            flex-wrap: wrap;
            align-items: center;
            align-content: center;
        }

        .logo {
            opacity: 0;
            width: 100%;
            margin-bottom: 40px;
            font-size: 37px;
            color: #fff;
            text-align: center;
            animation: 1s cubic-bezier(0.33, 0, 0.51, 1) 1.5s 1 normal forwards
                menuFadeIn;
            span {
                padding-left: 5px;
                color: rgb(186, 240, 176);
            }
        }
        .gnb {
            position: relative;
            width: 100%;
            display: flex;
            flex-direction: column;
            flex-wrap: wrap;

            .line {
                display: none;
                &.on {
                }
            }
            li {
                opacity: 0;
                animation: 1s cubic-bezier(0.33, 0, 0.51, 1) 1 normal forwards
                    menuFadeIn;
                width: 112px;
                margin: 20px auto;
                padding-bottom: 7px;
                text-align: center;
                color: #b1d2d3;
                border-bottom: dotted 2px #7b7b7b;
                a {
                    &:hover {
                        color: rgb(43, 174, 41);
                    }
                }
            }
            @for $i from 1 through 6 {
                li:nth-child(#{$i}) {
                    animation-delay: $i * 0.1s + 1.5s;
                }
            }
        }
    }
}
