.departmentConts {
    position: relative;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    background: #001f33;
    transform: translateY(0px);
    transition: all 1s ease-in-out 1s;
    z-index: 1;
    .departSwiper {
        width: 100%;
        height: 100%;
        top: 0;
        position: absolute;
        .swiper-wrapper-wrapper {
            position: absolute;
            top: 50%;
            width: 100%;
            transform: translateY(-50%) rotate(-20deg);
        }

        .tit {
            position: absolute;
            top: 50%;
            left: 50%;
            margin: -524px 0 0 -561px;
            z-index: 2;
            font-size: 60px;
            font-weight: 400;
            color: #f3efc6;
            opacity: 0;
            transition: all 0.5s;
            transition-delay: 0.5s;
            transform: rotate(20deg);
            &.on {
                opacity: 1;
            }

            span {
                font-family: "Cormorant", serif;
                font-size: 40px;
                font-style: italic;
                font-weight: 300;
            }

            div {
                margin: 4px 0 0 341px;
            }
        }

        .swiperImg {
            width: 330px;
            height: 400px;
            margin: 0 32px;
            transform: rotate(20deg);
            filter: blur(3px) grayscale(0.8);
            transition: all 0.5s;
            opacity: 0.6;
            .creator {
                position: relative;
                top: 0;
                left: 0;
                width: 100%;
                text-align: right;
                padding-bottom: 5px;
                color: #ccd5db;
                font-family: "Cormorant";
                transition: all 0.7s ease-in-out 0s;
            }
            .logoBtn {
                // display: flex;
                opacity: 0;
                width: 70px;
                height: 70px;
                margin-top: 30px;
                margin-right: 20px;
                background: transparent;
                border: 1px solid #ffffff00;
                // color: #ffffffa6;
                border-radius: 13px;
                align-items: center;
                justify-content: center;
                &:hover {
                    // background-color: #fff;
                    border: 2px solid #ffffffa6;
                    border-radius: 13px;
                }
            }
            .backBtn {
                display: flex;
                opacity: 0;
                width: 120px;
                height: 35px;
                margin-top: 30px;
                font-size: 17px;
                font-weight: 300;
                background: transparent;
                border: 1px solid #ffffffa6;
                color: #ffffffa6;
                align-items: center;
                justify-content: center;
                &:hover {
                    background-color: #fff;
                    font-weight: 500;
                    color: #073b3e;
                }
            }

            img {
                object-fit: cover;
                width: 100%;
                height: 100%;
                transition: all 0.5s;
            }

            span {
                display: block;
                position: relative;
                top: 0;
                left: 0;
                width: 100%;
                text-align: right;
                padding-top: 2px;
                font-size: 16px;
                color: #a1b5c2;
                font-family: "Cormorant";
                transition: all 0.7s ease-in-out 1s;
            }

            .conts {
                opacity: 0;
            }
        }

        .swiper-slide-active {
            z-index: 1;
            transform: rotate(20deg) scale(1.4);
            filter: blur(0px) grayscale(0);
            opacity: 1;
            transition: all 1s;
            img {
                opacity: 1;
                cursor: pointer;
                transition: all 0.5s;
                &:hover {
                    animation: 1.5s cubic-bezier(0.28, 0.84, 0.42, 1) 0s 1
                        normal both running departActiveImgBounce;
                }

                @keyframes departActiveImgBounce {
                    0% {
                        transform: scale(1, 1) translateY(0);
                    }
                    10% {
                        transform: scale(1.1, 0.9) translateY(0);
                    }
                    30% {
                        transform: scale(0.9, 1.1) translateY(-100px);
                    }
                    50% {
                        transform: scale(1.05, 0.95) translateY(0);
                    }
                    57% {
                        transform: scale(1, 1) translateY(-7px);
                    }
                    64% {
                        transform: scale(1, 1) translateY(0);
                    }
                    100% {
                        transform: scale(1, 1) translateY(0);
                    }
                }
            }
        }

        .swiper-pagination-progressbar {
            top: 0px;
        }

        .swiper-pagination-progressbar-fill {
            background-color: #567c94;
        }

        .swiper-button-next,
        .swiper-button-prev {
            color: #646c70;
        }
    }

    &.active {
        background-color: #073b3e;
        transition: all 1s ease-in-out 0.5s;
        transform: translateY(-101px);
        z-index: 1;
        .departSwiper {
            .swiper-wrapper-wrapper {
            }

            .tit {
                opacity: 0;
                transition: all 1s ease-in-out 0.3s;
            }

            .swiperImg {
                opacity: 0;
                transition: all 1s;
                .creator {
                }

                img {
                }

                span {
                }
            }

            .swiper-slide-active {
                z-index: 1;
                width: 600px;
                height: 750px;
                transform: rotate(20deg) scale(1);
                opacity: 1;
                margin: -314px 0 0 -395px;
                transition: all 1.1s ease-in-out 0.8s;
                img {
                    width: 100%;
                    height: 100%;
                    opacity: 1;
                    border-radius: 10px;
                    transition: all 1.1s ease-in-out 0.8s;
                    animation-play-state: paused;
                }

                .creator {
                    top: 30px;
                    left: 655px;
                    font-size: 29px;
                    transition: all 1s ease-in-out 1.1s;
                }
                .backBtn {
                    opacity: 1;
                    transition: opacity 1s ease-in-out 2.1s,
                        background-color 0.5s;
                    cursor: pointer;
                }
                .logoBtn {
                    opacity: 1;
                    transition: opacity 1s ease-in-out 2.1s,
                        background-color 0.5s;
                    cursor: pointer;
                }
                span {
                    margin-top: 15px;
                    font-size: 20px;
                }
                .contsWrap {
                    position: relative;
                    left: 670px;
                    top: -720px;
                }

                .conts {
                    color: #fff;
                    font-size: 16px;
                    font-weight: 300;
                    line-height: 1.6;
                    max-height: 620px;
                    padding-right: 16px;
                    opacity: 1;
                    overflow-y: auto;
                    text-align: justify;
                    transition: all 0.8s cubic-bezier(0.31, 0.25, 0.71, 0.73)
                        1.5s;
                    width: 100%;
                    .mb20 {
                        margin-bottom: 20px;
                    }
                }
            }

            .swiper-pagination-progressbar {
                opacity: 0;
                transition: all 1s ease-in-out 1s;
            }

            .swiper-pagination-progressbar-fill {
            }

            .swiper-button-next,
            .swiper-button-prev {
                opacity: 0;
                transition: all 1s ease-in-out 0.5s;
            }
        }
    }
}

